@import '../../../@theme/index';

.transactions_hide,
    .transactions_view {
        box-shadow: -2px 2px 8px rgba(0, 0, 0, 0.3);       
        position: absolute;
        // border-left: 1px solid $borderGray;
        // border-bottom: 1px solid $borderGray;
        width: 36%;
        box-sizing: border-box;
        background-color: #fff;
        height: auto;
        max-height: calc(100% - 75px);
        overflow-y: auto;      
        transition: all 0.5s;
        top: 75px;
        
        @media (max-width: $sm){
            width: 100%;
           
        };      
        .download {           
            text-align: center;
            color: #2F80ED;
            font: 400 23px/60px $roboto;
            border-bottom: 1px solid $borderGray;
        }
        .close {
            padding: 5px;
            float: right;
            opacity: 0.6;
            transform: scale(0.6);
            cursor: pointer;
        }
        .head_transaction {
            padding: 0 30px;
            font: 600 25px/70px $roboto;
            text-align: left;
            border-bottom: 1px solid $borderGray;
            @media (max-width: $sm){
                font: 600 18px/50px $roboto;
               
            };    
            span {
                float: right;
                font: 300 16px/70px $roboto;
                color: #bdbdbd;
                @media (max-width: $sm){
                    font: 600 18px/50px $roboto;
                   
                };    
                b {
                    font: 400 16px/50px $roboto;
                    color: rgb(11, 12, 12);
                }
            }  
        }
        .transaction_logo_block {
            display: flex;           
            justify-content: space-between;
            align-self: center;           
            border-bottom: 1px solid $borderGray;
            background-color: #f2f2f2;
            padding: 20px 40px 20px 30px ;
            @media (max-width: $sm){
                padding: 10px 20px 10px 10px ;              
            };    
            span {
                font: 600 23px/48px $archivo;
                vertical-align: top;
                display: inline-block;
                @media (max-width: $sm){
                    font: 600 18px/48px $archivo;                   
                };    
            }            
            p {
                font: 400 18px/25px $roboto;
                color:rgb(0, 169, 157);
                @media (max-width: $sm){
                    font: 400 16px/20px $roboto;
                   
                };     
            }
        }
        h2 {
            font: 500 20px/45px $roboto;
            text-align: center;
        }
        h4 {
            font: 500 16px/25px $roboto;
            text-align: left;
            padding-left: 20px;
        }
        .transaction_table {
            margin-top: 20px;           
            .transaction_table_header {              
               
                color:  #546E7A;
                border-bottom: 1px dashed #e0e0e0;
                
                font: 500 20px/45px $roboto;
                padding: 0 20px;
                span {
                    float: right;
                }              
            }
        }
        .transaction_data {
            padding: 30px 20px;
            p {
                display: flex;
                justify-content: space-between;
                font: 700 18px/40px $roboto;
                span{
                    b{
                        color: #27AE60;
                        width: 80%;
                        display: block;
                        font: 400 16px/20px $roboto;
                    }
                }
                img {
                    height: 22px;
                    width: 25px;
                    margin: 8px 0 0;
                    vertical-align: top;
                }
                img.revolve {
                    transform: rotate(180deg);
                }           
            }
        }
        .transaction_total {
            padding: 0 20px;
            background-color: #f2f2f2;
            color: #333;
            font: 500 16px/55px $roboto;
            span {
                float: right;
            }
            p:last-of-type {
                font: 700 18px/55px $roboto;
            }
        }
        footer {
            padding: 30px 20px;
            color: #828282;
            font: 500 18px/55px $roboto; 
            span {
                float: right;                   
            }  
               
                
           
        }
    }
    .transactions_view {       
        left: 64%;       
        @media (max-width: $sm){
            width: 100%;
            left: 0;
         };    
    }
   
      