@import '../../@theme/index';
$first_img: url('/assets/images/whoweare/whoweare.png');
$two_img: url('/assets/images/whoweare/image2.png');
$three_img: url('/assets/images/whoweare/image3.png');
$four_img: url('/assets/images/whoweare/image4.png');
$two_img_back: url('/assets/images/whoweare/back2.png');
//$three_img_back: url('/assets/images/whoweare/back3.png');
$three_img_back: url('/assets/images/whoweare/back3_1.png');
$four_img_back: url('/assets/images/whoweare/back4.png');

@import url('https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap');

$transition:  all  1s ease-out;

.whoweare {
    & h1 {       
        font: 700 45px/55px  'Bitter', serif;                      
        letter-spacing: 0.01em;
        transition: $transition;
        text-align: left;
        @media  (max-width: $xl) {
            font: 700 30px/35px  'Bitter', serif; 
        }
        @media (max-width: $md) {
            font: 700 19px/25px  'Bitter', serif;
        }         
    }
    & button {       
        height: 46px;               
        border: none;
        width: 190px;
        transition: all  1s ease-out;
        color: #ffffff;              
        background: $btnBlueBg;
        box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.22);
        border-radius: 23px;
        font-family: $roboto;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 21px;
        letter-spacing: 0em;
        @media  (max-width: $xl) {
            font: 700 16px/35px  $roboto;
            height: 35px;
            width: 150px;   
        }
        @media (max-width: $md) {
            font: 500 16px/25px  $roboto;
            height: 25px;
            width: 150px;   
        }                
    } 
    position: relative;    
    &__firstblock {
        background-image: $first_img;
        background-repeat: no-repeat;
        background-size: cover;
        height: 593px; 
        z-index: 1;      
        position: relative;
        @media  (max-width: $xl) {
            height: 500px;
        }
        @media  (max-width: $md) {
            height: 250px;
            background-size: 80%;
            background-position: right top;
        }        
        &__one,
        &__two {
            width: 340px;
            height: 77px;
            position: absolute;           
            transform: skewX(19.5deg);
            @media (max-width: $xl) {
                width: 200px;
                height: 45px;
            }
            @media (max-width: $md) {
                display: none;
            }  
            & p {
                text-align: center;
                font: 300 36px/77px  $roboto;               
                font-style: normal;               
                letter-spacing: 0em;
                color: #ffffff;
                transform: skewX(-19.5deg);
                & b {
                    font-weight: 500;
                }
                @media (max-width: $xl) {
                    font: 300 20px/45px $roboto;    
                }
                
            }
        }
        &__one {
            top: 60px;
            left: 36%;          
            background-color: #34A852;
            @media  (max-width: $xl) {
                top: 10px;
            };
           
                      
        }
        &__two {          
            top: 150px;
            left: 38.3%;
            background-color: #4285F4;
            @media  (max-width: $xl) {
                top: 70px;
            };            
        }
        & button {
            margin-top: 20px;
            @media  (max-width: $md-extra) {
                margin-top: 15px;
            };           
        }
        &__title {
            width: 40%;           
            position: absolute;
            top: 50px;
            left: 14%;
            @media  (max-width: $xl) {
                top: 5px;
            };
            @media  (max-width: $md-extra) {
                left: 3%;
                width: 100%;
            };                  
            & h1{               
                & span {
                    color: green;
                }
            }
            & p {
                margin-top: 30px;                
                font: 300 22px/34px  $roboto;                
                letter-spacing: 0em;
                text-align: left;
                @media (max-width: $xl) {
                    font: 300 16px/24px  $roboto;    
                }
                @media (max-width: $md) {
                    font: 300 14px/18px  'Bitter', serif;
                    margin-top: 15px;
                }         

            }
        }
        &__videoBlock {
            margin-left: 9.5%;
            width: 50%;
            z-index: 10;
            // top: 550px;
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
            @media (max-width: $xxl) {
              margin-left: 5%;
              width: 55%;
              align-items: center;
            }
            @media (max-width: $md) {
              opacity: 0;
              visibility: hidden;
            }
            &__wrap {
              width: 514px;
              height: 319px;
              position: relative;
              top: -80px;
              left: 80px;
              transition: 0.5s;
              transform: scale(1);
      
              @media (max-width: $xxl) {
                width: 100%;
                height: 75%;
              }
            }
            &__videoWrap {
              width: 513px;
              height: 319px;
              box-sizing: border-box;
              border: 1px solid #4285F4;
              @media (max-width: $xl) {
                width: 413px;
                height: 219px;
              }             
            }              
            &__videoMenu {
                height: 5%;
                width: 514px;
                background: $blueText;
                border-top-left-radius: 6px;
                border-top-right-radius: 6px;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                @media (max-width: $xl) {
                    width: 413px;
                   
                  }            
            }        
            &__full {
                margin-right: 10px;
                display: block;
            }
            & video {
                width: 100%;
                height: 100%;
                -webkit-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                -moz-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                border-bottom-left-radius: 6px;
                border-bottom-right-radius: 6px;
                object-fit: cover;
            }              
            &__controllers {
              position: absolute;
              width: 100%;
              height: 100%;
              margin-top: 20px;
              display: flex;
              justify-content: center;
              align-items: center;
              top: 0;
              &__stopVideoBtn {
                transition: 0.5s;
                opacity: 0;
                visibility: hidden;
              }
              & button {
                background: none;
                border: none;
                width: 72px;
                height: 72px;
                transition: 0.5s;
                &:hover {
                  transform: scale(1.1);
                }
                & img {
                  max-width: 100%;
                  border-radius: 50%;
                }
              }
            }       
        }    
    }
    &__secondblock {
        height: 905px;
        position: relative;
        background-color: #ffffff;        
        background-image: $two_img_back;
        background-repeat: no-repeat;
        background-size: 100%;
        @media  (max-width: $md) {
            height: 280px;
        }     
        &__h {
            position: absolute;
            top: 4vw;
            z-index: 0;
            transition: $transition;
            opacity: 0;
            width: fit-content;
            left: 51%;
            & span {
                color: #4285F4;
            }
            @media  (max-width: $md) {
                left: 30%;
            }     
        }
        &__button {
            position: absolute;
            left: 14%;
            transition: $transition;
            bottom: 0;
            opacity: 0;
            @media  (max-width: $md) {
                left: 3%;
            }     
        }
        &__p {
            font: 300 26px/48px $roboto;
            position: absolute;
            top: 25vw;
            left: -34%;
            z-index: 1;
            opacity: 0;
            transition: $transition;
            & b {
                font-weight: 500;
            }
            @media (max-width: $xl) {
                font: 300 18px/25px $roboto;    
            }
            @media (max-width: $md) {
                font: 300 15px/20px $roboto;
                top: 70px;
                left: 1%;  
            }  
        }
        &__image {
            background-image: $two_img;
            background-repeat: no-repeat;
            background-size: auto 30vw;
            background-position: 0 130%;
            transition: $transition;
            height: 100%;
            width: 50%;
            position: absolute;
            right: 0;
            bottom: 0;      
            @media (max-width: $xl) {
               
            }
            @media (max-width: $md) {
                // height: 50vw;
                width: 50vw;
                background-size: contain;
                // top: 15vw; 
            }  
            // @media (max-width: $sm-extra) {
            //     height: 200px;
            //     width: 200px;
            //     background-size: contain;
            //     bottom: 20px; 
            // }  
        }
    }
    &__threeblock {
        height: 800px;
        z-index: 1;
        position: relative;
        background-image: $three_img_back;
        background-repeat: no-repeat;
        background-size: 100%;
        // background-position:  0;
        @media  (max-width: $md) {
            height: 280px;
        } 
        @media  (max-width: $sm) {
            height: 230px;
        }           
        &__h {
            position: absolute;
            // top: 45%;
            opacity: 0;
            transition: $transition;
            left: 52.5%;
            & span {
                color: #EA4335F4;
            }
            @media  (max-width: $md) {
               left: 50%;

            }    
        }
        &__button {
            position: absolute;
            left: 52.5%;
            bottom: 0;
            opacity: 0;
            transition: $transition;
            @media  (max-width: $md) {
                left: 50%;
 
             }    
        }
        &__image {
            background-image: $three_img;
            background-repeat: no-repeat;
            background-size: auto 25vw;
            background-position: 100% 450%;
            transition: $transition;
            height: 100%;
            width: 50%; 
            @media (max-width: $xl) {
                background-size: auto 25vw;
                
            }               
            @media (max-width: $md) {
                height: 50vw;
                width: 50vw;
                background-size: 80%;
                bottom: 20px; 
            }  
        }  
    }
    &__fourblock {
        height: 715px;
        position: relative;
        background-image: $four_img_back;
        background-repeat: no-repeat;
        background-size: 60%;
        background-position: left center;
        @media  (max-width: $md) {
            height: 280px;
            
        }     
        &__head {
            font: 300 38px/45px $roboto;           
            letter-spacing: 0em;
            text-align: center;
            z-index: 0;
            transition: $transition;
            margin: 0 auto;
            transform: translateY(120px);
            opacity: 0;
            & b {
                font-weight: 400;
            }
            @media  (max-width: $md) {
                left: 30%;
                font: 300 18px/22px $roboto;      
            }     
        }
        &__h {
            position: absolute;
            top: 29%;
            opacity: 0;
            transition: $transition;
            left: -24%;
            & span {
                color: green;
            }
             @media (max-width: $xl) {
                top: 38%;   
            }    
            @media  (max-width: $md) {
                left: 14%;
                top: 30px;
                font: 300 18px/22px $roboto;
            } 
           
        }
        &__button {
            position: absolute;
            left: -33%;
            top: 68%;
            opacity: 0;
            transition: $transition;
            @media (max-width: $xl) {
                top: 60%;   
            }    
            @media  (max-width: $md) {
                left: 10%;
                top: 60%;
               
            }     
        }
        &__p {
            font: 300 26px/35px $roboto;
            position: absolute;
            top: 47%;
            left: -24%;
            opacity: 0;
            z-index: 10;
            transition: $transition;
            @media (max-width: $xl) {
                font: 300 20px/30px $roboto;    
            }
            @media  (max-width: $md) {
                left: 10%;
                top: 31%;
                font: 300 16px/20px $roboto;
            }                
        }
        &__image {
            background-image: $four_img;
            background-repeat: no-repeat;
            background-size: auto 30vw;
            background-position: 100% 530%;
            transition: $transition;
            height: 100%;
            width: 50%;
            position: absolute;
            right:  0;
            // bottom: 155px;
            @media (max-width: $xl) {
                background-size: auto 60%;
                
            }    
            @media (max-width: $md) {
               
                background-size: 80%;
                bottom: 0; 
                right:  2%;
            }
            @media (max-width: $sm) {               
                background-size: 80%;
                bottom: 10%; 
                right:  2%;
            }    
        }            
    }
}