@import '../../../@theme/index';

.bs_ended {
  width: 100%;
  &__wrap {
    width: 75%;
    padding: 0 30px;
    box-sizing: border-box;
    margin: auto;
  }
  &__header {
    margin-top: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    &__text {
      width: 40%;
      &__title {
        font-family: $bitter;
        font-size: 40px;
        line-height: 52px;
        font-weight: bold;
        color: $darkGrayText;
        text-align: left;
        margin-bottom: 45px;
      }
      &__description {
        width: 70%;
        font-family: $roboto;
        font-weight: 400;
        font-size: 24px;
        line-height: 32px;
        color: $sponsorText;
      }
    }
    &__image {
      width: 60%;
      & img {
        max-width: 100%;
        object-fit: contain;
      }
    }
  }
  &__partners {
    width: 100%;
    margin-top: 75px;
    &__title {
      font-family: $roboto;
      font-size: 28px;
      font-weight: 300;
      color: $squareBenefitText;
      text-align: center;
    }
    &__brands {
      display: flex;
      flex-wrap: wrap;
      margin-top: 25px;
      margin-bottom: 80px;
      &__item {
        display: flex;
        width: 16.6%;
        box-sizing: border-box;
        padding: 30px 50px;
        border-bottom: 1px solid $btnLightGrayBg;
        border-right: 1px solid $btnLightGrayBg;
        &:nth-child(6n) {
          border-right: none;
        }
        &:nth-last-child(-n + 6) {
          border-bottom: none;
        }
        & img {
          max-width: 100%;
          object-fit: contain;
          margin: auto;
        }
      }
    }
  }
}
