@import '../../../@theme/index';

.reasonsForSponsoring {
  padding: 0 0 40px;
  background: $bgMain;
  &.white {
    background: $whiteText;
  }
  @media (max-width: $xl) {
    padding: 150px 0 70px;
  }
  @media (max-width: $sm) {
    padding: 50px 0 ;
  }
  // background: $bgMain;
  &__wrap {
    font-family: $roboto;
    text-align: center;
    @media (max-width: $sm-mini) {
      width: 70%;
      margin: auto;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 45px;
    font-family: $bitter;
    color: $darkestGrayText;
    @media (max-width: $sm-mini) {
      font-weight: 500;
      font-size: 28px;
      line-height: 29px;
    }
  }
  &__description {
    font-size: 24px;
    line-height: 32px;
    font-family: $roboto;
    font-weight: 300;
    color: $darkGrayText;
    margin: 35px auto;
    width: 57.5%;
    @media (max-width: $sm) {
      width: 100%;
    }
    @media (max-width: $sm-mini) {
      margin: 10px auto 20px;
     
      font-size: 12px;
      line-height: 16px;
      text-align: left;
      font-weight: normal;
    }
  }
  &__ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    margin: auto;
    @media (max-width: $lg) {
      flex-wrap: wrap;
    }
  }
}
