@import '../../../@theme/index';

.main{
    .pdf {
        width: 1370px;
        position: fixed;
        top: 70px;
        left: calc(50% - 685px);
        height: 100vh;
        background-color:  rgba(0, 0, 0, 0.5),
    }
    .close_pdf {
        position: relative;
        z-index: 100;
        float: right;       
    }     
    .bigblock {
        width: 100%;
        overflow-x: auto;
        white-space: nowrap;
    }
    .nextblock {
        width: 1369px; 
        position: relative;       
        overflow: hidden;
        padding-bottom: 30px;
    }  
    .head {
        height: 200px;
        padding: 1vw;
        text-align: center;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        align-items: center;
        @media  (max-width: $sm) {
            flex-direction: column;
            align-items: flex-start;
            height: auto;            
        }
        .logo {
            height: 100%;            
            width: 30%;
            border-right: 1px solid #ddd;
            position: relative;
            background-position: left center;
            background-size: 40%;
            background-repeat: no-repeat;
            @media  (max-width: $xxl) {
                width: 24%;
            }            
            @media  (max-width: $sm) {
                width: 102%;
                margin: -1vw;          
                height: 80px;
                background-size: 58px 56px;
                background-color: #f2f2f2;
                background-position: 3vw center;
                font: 600 22px/80px $roboto; 
               text-align: left;      
            }  
            padding-left: 100px;
            font: 600 22px/160px $roboto;
               
        }    
        .pledged, 
        .receved { 
            height: 100%;
            text-align: left;
            width: 25vw;
            padding: 1vw;
            border-right: 1px solid #ddd;           
            box-sizing: border-box;
            @media  (max-width: $sm) {
                width: 100%;
                border: none;
                height: 110px; 
                         
            }         
            p { 
                font-size: 1vw;
                margin-top: 30px;               
                font-family: $roboto;
                color: rgb(0, 0, 0);
                line-height: 1.5;
                @media (max-width: $xxl) {
                    font-size: 18px;
                }                           
            } 
            h2 {
                font: 400 45px/65px $roboto;                
                color: rgb(66, 133, 244);               
                @media (max-width: $xxl) {
                    font-size: 29px;
                }              
            }
            b {
                font-size: 34px;
            }
        }    
        .receved h3 {
            font: 400 45px/140px $roboto;          
            span {                   
                font-size: 16px;                    
                color: rgb(137, 137, 137);
                line-height: 1.2;                     
            }
            @media (max-width: $xxl) {
                font-size: 29px;
            }                  
        }
        .sponsors {
            height: 100%;
            text-align: left;
            padding: 1vw 0 1vw 1vw;
            width: auto;
           
            p {
                display: inline-block;               
                font-size: 14px;
                width: 32%;
                margin: 10px 4px 0 0;
                font-family: $roboto;
                color: rgb(137, 137, 137);
                line-height: 1.071;
                span {
                    margin-top: 10px;
                    display: inline-block;                   
                }
                :last-of-type {
                    color: rgb(66, 133, 244);
                }                  
            }
            h2 {               
                font-size: 16px;
                font-family: $roboto;
                font-weight: 400;               
                line-height: 1.2;        
                margin-top: 20px;                  
            }
        }
    }
    .selected {
        background-color: rgb(66, 133, 244);
        padding: 0  20px;
        height: 60px;
        position: relative;
        .count_selected {
            border-right: 1px solid #fff;           
        }
        .actions_menu {            
            padding: 0;           
            border: 1px solid #ccc;
            background-color: #fff;
            color: black;
            display: none;
            position: absolute;
            top: 24px;
            left: 0;
            b {
                width: 150px;
                padding-left: 10px;               
                cursor: pointer;
                margin: 3px auto 0;
                display: inline-block;
            }
            b:hover {
                background-color: #ddd;
            }
        }
        .actions_menu:hover {
            display: inline-block;
        }
        .close {
            float: right;
            border-left: 1px solid #fff;
            padding: 10px 0 10px 25px;
            margin-top: 10px;
            cursor: pointer;
        }
        span {
            color: #fff;           
            display: inline-block;
            font: 400 18px/30px $roboto;
            margin-top: 13px;
            padding-right: 25px;
        }       
        p {
            border: 1px solid #94BBFC;
            color: #fff;      
            background-color: rgba(204, 204, 204, 0.5);
            background-position: 85% center;
            background-repeat: no-repeat;            
            height: 35px;
            position: relative;
            display: inline-block;
            width: 120px;
            font: 500 18px/35px $roboto;
            cursor: pointer;
            margin-left: 25px;
            text-align: left;
            padding-left: 15px;            
        }
        p:hover  .actions_menu {
            display: inline-block;
        }
    }
    .export {
        background-color: #f2f2f2;
        padding: 0  20px;
        height: 60px;
        @media (max-width: $sm) {           
           height: 130px;
           padding: 0  10px;
        }
        button {              
            border: 1px solid #bdbdbd; ;           
            border-radius: 2px;
            background-color: #f2f2f2;               
            width: 115px;
            height: 48px;
            float: right;
            margin-top: 6px;
            font: 600 14px/28px $roboto;               
            color: rgb(70, 70, 70);
            @media (max-width: $sm) {
                display: block;               
                margin-top: -110px;
            }
            img {
                vertical-align: sub;
                margin-right: 10px;
            }                        
        }
       .active,
       .unactive {
            display: inline;
            float: left;
            cursor: pointer;
            vertical-align: middle;
            font-size: 15px;
            height: 100%;
            font-family: "Roboto";
            color: rgb(137, 137, 137);
            box-sizing: border-box;
            line-height: 60px;
            text-align: left;
            margin-right: 40px;
            padding: 10px 20px;
            @media (max-width: $sm) {
                margin-right: 0;
                padding: 70px 10px 0;
            }
              
        }        
        .active {
            color: rgb(66, 133, 244);
            border-bottom: 1px solid rgb(66, 133, 244);
        }
        .unactive {           
            border: none;
        }

       
    }
   
    .date {
        padding: 20px 0 10px;
        margin: 0 20px;      
        border-bottom: 1px solid #ddd;       
        font-size: 20px;
        font-family: $roboto;
        color: rgb(172, 172, 172);
        line-height: 1.2;      
    }
    .header_table, 
    .table {
        padding: 10px 0;       
        font-size: 16px;
        margin: 0 20px;
        font-family: $roboto;
        color: rgb(84, 110, 122);
        font-weight: 400;
        line-height: 1.333;       
        border-bottom: 1px dotted #ddd;
        input {
            height: 17px;
            width: 17px;
            margin: 2px 10px 0;
            vertical-align: top;
        }
        input:checked {
            background-color: green;
        }
        span, 
        label {           
            text-align: left;           
            display: inline-block;
            vertical-align: top;
        }
        .community_sponsored {
            width: 23%;
            padding-left: 32px;
            display: inline-block;
            font-size: 14px;            
            b {
                color: #ccc;
            }
            strong {
                font-weight: 700;
                font-size: 18px;
            }                        
            b.sponsored:last-of-type {
                color: rgb(66, 133, 244);
                white-space: normal;               
                line-height: 1.286;                          
            }        
        }
        span {
            width: 9%;
            b {
                display: block;
                text-align: left;
            }
        }       
        .payment_status {
            text-align: center;
            border-left: 1px solid #ddd;
            border-right: 1px solid #ddd;           
            display: inline-block;               
            button {
                height: 24px;
                width: 82px;
                background-color: #2F80ED;
                border: none;
                border-radius: 50px;
                display: block;
                color: #fff;
                margin: 10px auto;        
                font-size: 14px;  
            }
            button:hover {
                background-color: #114385;
            }
            b {              
               font-size: 15px;
               font-family: $roboto;
               color: rgb(255, 200, 46);
               line-height: 2;
               text-align: center;       
               display: inline-block;
               border-radius: 2px;
               background-color: rgb(254, 248, 232);
               padding: 0 10px;                              
            }
            span {
                display: block;
                width: fit-content;
                padding: 1px 5px;
                text-align: center;
                margin: 4px auto;                
                color: rgb(66, 133, 244);                
            }
            .pending {                
                border-radius: 2px;
                background-color: rgb(254, 248, 232);               
                color: rgb(255, 200, 46);                                                 
            }
            .unpaid {                
                border-radius: 2px;
                background-color: rgb(253, 235, 235);
                color: rgb(241, 80, 80);                 
            }
            .paid {               
                border-radius: 2px;
                background-color: rgb(235, 249, 235);               
                color: rgb(96, 183, 96);                 
            }
            .received {               
                border-radius: 2px;
                background-color: rgb(255, 244, 255);               
                color: rgb(189, 140, 191);                
            }
            .fufilled {                
                border-radius: 2px;                
                background-color: rgb(255, 244, 239);               
                color: rgb(246, 142, 86);                 
            }
            .completed {               
                border-radius: 2px;               
                background-color: rgb(236, 255, 253);                
                color: rgb(0, 169, 157);          
            }            
            // span:hover {
            //   background-color: #f2f2f2;
            // }          
        }
        span:last-of-type {
            text-align: center;            
        }
        span:last-of-type(:hover) {
            color: rgb(8, 48, 88);
        }       
        img {
            height: 22px;
            width: 25px;
            margin: -2px 8px 0 -30px;
            vertical-align: top;
        }
        img.revolve {
            transform: rotate(180deg);
        }           
        strong {
           vertical-align: super;
           white-space: normal;
        }          
    }
    .table {
        color: rgb(11, 12, 12);
        border-bottom: 1px solid $borderGray;
        display: flex;
        justify-items: center;
        div:last-of-type {
            border: none;
            width: 11.5%;
        }
    }
    .total {
        font: 500 20px/30px $roboto;
        margin-left: 52%; 
        span {
            padding: 0 10px 0 25px ;                          
        }              
    }
    .header_table {
        font-weight: 500;
        color: #546e7a;
    }
    
}

  